import { AbstractEthereumConnector, } from "@renproject/multiwallet-abstract-ethereum-connector";
export class EthereumWalletConnectConnector extends AbstractEthereumConnector {
    constructor(options) {
        super(options);
        this.supportsTestnet = false;
        this.handleUpdate = () => {
            this.getStatus()
                .then((...args) => {
                this.emitter.emitUpdate(...args);
            })
                .catch(async (...args) => {
                console.error("Failed to get status");
                this.deactivate(...args);
            });
        };
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        this.activate = async () => {
            if (this.debug)
                console.debug("activating");
            await this.cleanup();
            if (this.debug)
                console.debug("cleaned up old listeners");
            // No good typings for injected providers exist...
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            const provider = await this.getProvider();
            if (this.debug)
                console.debug("got provider");
            if (!provider) {
                throw Error("Missing Provider");
            }
            if (!provider.wc.connected) {
                if (this.debug)
                    console.debug("creating session");
                await provider.wc.createSession({
                    chainId: Number(Object.keys(this.rpc)[0]),
                });
            }
            if (this.debug)
                console.debug("enabling");
            await provider.enable().catch((error) => {
                console.error(error);
                // TODO ideally this would be a better check
                if (error.message === "User closed modal") {
                    this.emitter.emitError(new Error("User rejected request"));
                }
                throw error;
            });
            if (this.debug)
                console.debug("enabled");
            provider.on("close", this.deactivate);
            provider.on("networkChanged", this.handleUpdate);
            provider.on("accountsChanged", this.handleUpdate);
            provider.on("chainChanged", this.handleUpdate);
            return this.getStatus();
        };
        this.getProvider = async () => {
            if (this.debug)
                console.debug("getting provider");
            if (this.provider)
                return this.provider;
            if (this.debug)
                console.debug("getting module");
            // eslint-disable-next-line @typescript-eslint/no-shadow
            const WalletConnectProvider = await import("@walletconnect/web3-provider").then((m) => { var _a; return (_a = m === null || m === void 0 ? void 0 : m.default) !== null && _a !== void 0 ? _a : m; });
            if (this.debug)
                console.debug("building provider");
            this.provider = new WalletConnectProvider({
                bridge: this.bridge,
                rpc: this.rpc,
                qrcode: this.qrcode,
                pollingInterval: this.pollingInterval,
            });
            return this.provider;
        };
        this.deactivate = async (reason) => {
            await this.cleanup();
            const provider = await this.getProvider();
            if (provider.removeListener) {
                // eslint-disable-next-line @typescript-eslint/no-misused-promises
                provider.removeListener("close", this.deactivate);
            }
            await provider.close();
            this.provider = undefined;
            this.emitter.emitDeactivate(reason);
        };
        this.bridge = options.bridge;
        this.rpc = options.rpc;
    }
    // Get default web3 account
    async getAccount() {
        return (await this.getProvider())
            .send("eth_accounts")
            .then((accounts) => accounts[0]);
    }
    // Cast current ethereum network to Ren network version or throw
    async getRenNetwork() {
        if (!this.provider)
            throw new Error("not initialized");
        return this.networkIdMapper(await this.provider.send("eth_chainId"));
    }
    async cleanup() {
        const provider = await this.getProvider();
        if (provider.removeListener) {
            // eslint-disable-next-line @typescript-eslint/no-misused-promises
            provider.removeListener("close", this.deactivate);
            provider.removeListener("networkChanged", this.handleUpdate);
            provider.removeListener("accountsChanged", this.handleUpdate);
            provider.removeListener("chainChanged", this.handleUpdate);
        }
    }
}
